@use './main.scss';

@mixin shape {
    position: absolute;
    margin-top: -1px;
    margin-left: -1900px;
    top: 0;
    transform: scaleY(-1) scaleX(-1);

    @include smallScreen{
        margin-left: -1570px;
    }
    @include midScreen{
        margin-left: 330px;
    }
    @include largeScreen{
        margin-left: 300px;
    }
    @include xlargeScreen{
        margin-left: 250px;
    }
    z-index: 10;
}
@mixin front {
    position: absolute;
    bottom: 0;
    width: 850px;
    height: 294px;
    margin-bottom: -10px;
    margin-left: -141px;
    transition: filter .5s ease-in-out;

    @include smallScreen{
        right: 0;
        width: 1000px;
        height: 345px;
        margin-bottom: 0px;
        margin-right: -80px;
    }
    @include midScreen{
        right: auto;
        width: 1250px;
        height: 432px;
        margin-left: -120px;
    }
    @include largeScreen{
        width: 1500px;
        height: 518px;
        margin-left: 150px;
    }
    @include xlargeScreen{
        width: 2000px;
        height: 691px;
        margin-left: 150px;
    }
    z-index: 9;
}
@mixin back {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 750px;
    height: 259px;
    margin-right: -176px;
    transition: filter .5s ease-in-out;

    @include smallScreen{
        width: 1100px;
        height: 380px;
        margin-right: -150px;
    }
    @include midScreen{
        right: auto;
        width: 1250px;
        height: 432px;
        margin-left: 240px;
    }
    @include largeScreen{
        width: 1500px;
        height: 518px;
        margin-left: -100px;
    }
    @include xlargeScreen{
        width: 2000px;
        height: 691px;
        margin-right: -100px;
    }
    z-index: 8;
}

.sent{
    display: flex;
    position: absolute;
    margin-bottom: 555px;
    font-weight: 700;
    font-size: .90rem;
    color: #43A047;

    .icon{
        margin: 0.5px 7.5px 0px 7.5px;
    }
}

.text{
    display: flex;
    justify-content: center;
}

#contact{
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;

    .divider{
        position: absolute;
        width: 90%;
        height: 2px;
        background-color: var(--darkWhite);
        transition: background-color .5s ease-in-out;
    }

    .contact-container{
        width: 100%;
        margin-top: 100px;

        .contact-header{
            display: flex;
            justify-content: center;

            .contact-title{
                font-weight: 700;
                font-size: clamp(1.8rem, 1.25vw, 2rem);
                letter-spacing: 1px;
                color: var(--black);
                @include textShadow;
                @include underline(40px);
            }
        }

        .contact-body{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 101px;
            width: 100%;
            background-color: $blue;
            background-image: $blue-gradient2;
            height: 90vh;
            @include midScreen{
                height: 80vh;
            }
            @include largeScreen{
                height: 90vh;
            }
            overflow: hidden;

            .contact-imgs{
                display: flex;
                justify-content: flex-start;
                @include midScreen{
                    display: flex;
                    justify-content: center;
                }
                position: relative;
                width: 100%;
                height: 100%;
                &::before{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 5px;
                    background-color: var(--white);
                    margin-top: -2px;
                    transition: background-color .5s ease-in-out;
                }

                .nightGradient{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: $darkBlue;
                    background-image: $darkBlue-gradient2;
                    transition: opacity 1s ease-in-out;
                }
                .dayShape{
                    @include shape();
                }
                .nightShape{
                    @include shape();
                    transition: opacity .5s ease-in-out;
                }
                .landscape{
                    @include back();
                }
                .mail-box-open{
                    @include front();
                }
                .mail-box-closed{
                    @include front();
                }

                .con-stars-img{
                    position: absolute;
                    transform: scale(.75, .75);
                    transition: opacity 1s ease-in-out;
                    bottom: 0;
                    left: 50%;
                    z-index: 6;
                    margin-bottom: -100px;
                    margin-left: -1600px;
                    @include midScreen{
                        margin-left: -1700px;
                    }
                }
            }

            .contacts-form{
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                position: absolute;
                background-color: var(--lightWhite);
                border-radius: 5px;
                width: 300px;
                margin-bottom: 100px;
                @include smallScreen{
                    width: 400px;
                }
                @include midScreen{
                    width: 500px;
                    height: 500px;
                    margin-top: 60px;
                }
                @include largeScreen{
                    width: 600px;
                    height: auto;
                    //margin-bottom: 150px;
                }
                transition: background-color .5s ease-in-out;
                z-index: 20;
    
                .form{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;

                    .common{
                        display: flex;
                        justify-content: center;
                        gap: 15px;
                        input{
                            width: 117px;
                            @include smallScreen{
                                width: 141px;
                            }
                            @include midScreen{
                                width: 190px;
                            }
                            @include largeScreen{
                                width: 240px;
                            }
                        }
                    }

                    label {
                        display: flex;
                        flex-direction: column;
                        margin-top: 25px;
                        color: var(--darkGrey);
                        font-size: clamp(.9rem, 2vw, 1rem);
                        font-weight: 500;
                    }

                    input, textarea {
                        margin-top: 2.5px;
                        width: 250px;
                        height: 40px;
                        background-color: var(--white);
                        border: 1px solid var(--darkGrey);
                        border-radius: 5px;

                        //Input Text
                        padding: 7.5px;
                        font-family: 'Poppins', sans-serif;
                        font-size: clamp(.8rem, 2vw, .9rem);
                        font-weight: 400;
                        color: var(--darkGrey);

                        @include smallScreen{
                            width: 300px;
                        }
                        @include midScreen{
                            width: 400px;
                        }
                        @include largeScreen{
                            width: 500px;
                        }
                        transition: background-color .5s ease-in-out;
                    }

                    textarea {
                        height: 120px;
                        @include midScreen{
                            height: 140px;
                        }
                        @include largeScreen{
                            height: 160px;
                        }
                    }

                    .submit-btn{
                        cursor: pointer;     
                        background: none;
                        text-decoration: none;
                        border: 1px solid var(--darkGrey);
                        border-radius: 10px;
                        width: 250px;
                        height: 50px;
                        margin: 25px 50px 0px 50px;

                        @include smallScreen{
                            width: 300px;
                        }
                        @include midScreen{
                            width: 300px;
                            height: 55px;
                            margin: 25px 0px 0px 0px;
                            border-radius: 12.5px;
                        }
        
                        //Button
                        background-image: linear-gradient(to left, var(--darkGrey)  50%, transparent 50%);
                        background-size: 300%;
                        background-position: -50%;
                        transition: background-position .25s ease-in-out, color .25s ease-in-out, background-image .5s ease-in-out;
        
                        //Text
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        color: var(--white);

                        &:hover{
                            color: var(--black);
                            background-position: 0%;
                        }
                    }
                }
    
                .contact-list{
                    display: flex;
                    flex-direction: row;
                    list-style: none;
                    margin: 15px 0px 25px 0px;

                    a{
                        color: var(--darkGrey);
                        font-size: clamp(1.60rem, 1vw, 1.70rem);
                        margin-left: 15px;
                        transition: color .5s ease-in-out;
                        text-decoration: none;
                    }
                }
            }
        }
    }
}