$imgWidth: 200px;

@use './main.scss';

.text{
    display: flex;
    color: var(--black);
    justify-content: center;
}

#work{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 600px;
    background-color: var(--lightWhite);
    margin-top: 450px;

    @include smallScreen{
        margin-top: 400px;
    }
    @include midScreen{
        margin-top: 150px;
    }
    @include largeScreen{
        margin-top: 250px;
    }
    @include xlargeScreen{
        margin-top: 300px;
    }

    .work-container{
        width: 100%;
        max-width: 1640px;

        .work-header{
            display: flex;
            justify-content: center;
            flex-direction: column;
            margin-top: 100px;

            .work-title{
                font-weight: 700;
                font-size: clamp(1.8rem, 1.25vw, 2rem);
                letter-spacing: 1px;
                color: var(--black);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
            }
            
            .work-desc{
                font-weight: 500;
                font-size: clamp(1.25rem, 1.25vw, 1.5rem);
                letter-spacing: 1px;
                color: var(--black);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
                @include underline(40px);
            }
        }

        .work-body{
            margin-top: 100px;

            .work-grid{
                margin-top: 50px;
                display: grid;
                justify-content: center;
                grid-template-columns: auto;
    
                @media screen and (min-width: 470px){
                    grid-template-columns: auto auto;
                }  
                @media screen and (min-width: 720px){
                    grid-template-columns: auto auto auto;
                }
                @media screen and (min-width: 940px){
                    grid-template-columns: auto auto auto auto;
                }
    
                .workCard{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 200px;
    
                    .card-img{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: var(--white);
                        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                        border-radius: 10px;
                        width: $imgWidth;
                        height: $imgWidth;
                        z-index: 10;
                        overflow: hidden;
    
                        .nin-img{
                            width: $imgWidth - 25px;
                            height: 148.75px;
                        }  
    
                        &::before{
                            content: "";
                            position: absolute;
                            width: $imgWidth;
                            height: $imgWidth;
                            border-radius: 10px;
                            background-color: rgba(0, 0, 0, 0);
                            transition: background-color .2s ease-in-out, backdrop-filter .2s ease-in-out;
                        }
    
                        &:hover::before{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background-color: rgba(0, 0, 0, .5);
                            backdrop-filter: blur(5px);
                            content: "";
                            cursor: pointer;
                            font-weight: 700;
                            letter-spacing: 1px;
                            color: $white;
                        }
    
                        .card-text{
                            position: absolute;
                            margin-top: 10px;
                            color: $white;
                            z-index: 9;
                            text-align: center;
                            text-decoration: none;
                            font-size: clamp(.9rem, 1vw, 1.1em);
                            transform: translateY(75px);
                            opacity: 0;
                            transition: transform .2s ease-in-out, opacity .1s ease-in-out;
        
                            h4{
                                font-size: clamp(1rem, 1vw, 1.2em);
                            }
                            p:nth-child(4){
                                font-weight: 500;
                            }
                        }
    
                        &:hover{
                            .card-text{
                                transform: translateY(0px);
                                opacity: 1;
                            }
                        }
                    }
                }
            }

        }
    }
}