@use './main.scss';

.text{
    display: flex;
    justify-content: center;
}

#skills{
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 100px 0px 75px 0px;

    .skills-container{
        width: 100%;
        @include xlargeScreen{
            width: 75%;
        }
        max-width: 1340px;

        .skills-header{
            display: flex;
            justify-content: center;
            flex-direction: column;

            .skills-title{
                text-align: center;
                font-weight: 700;
                font-size: clamp(1.8rem, 1.25vw, 2rem);
                letter-spacing: 1px;
                color: var(--darkGrey);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
            }
            
            .skills-desc{
                text-align: center;
                font-weight: 500;
                font-size: clamp(1.25rem, 1.25vw, 1.5rem);
                letter-spacing: 1px;
                color: var(--darkGrey);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
                @include underline(40px);
            }
        }

        .skills-grid{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 100px 50px 0px 50px;
            gap: 40px 40px;

            @media screen and (min-width: 831px){
                margin: 100px 200px 0px 200px;
            }
            @media screen and (min-width: 1190px){
                margin: 100px 300px 0px 300px;
            }

            .skill-card{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;
                width: 110px;
                height: 110px;
                border-radius: 15px;
                @include bottomEffect;
                @include midScreen{
                    width: 120px;
                    height: 120px;
                }
                @include xlargeScreen{
                    width: 130px;
                    height: 130px;
                }
                img{
                    filter: grayscale(1) contrast(50%);
                    width: 85px;
                    z-index: 10;
                    @include midScreen{
                        width: 95px;
                    }
                    @include xlargeScreen{
                        width: 105px;
                    }    
                    transition: filter .2s ease-in-out, transform .2s ease-in-out;;
                }
                p{
                    position: absolute;
                    text-align: center;
                    color: var(--darkGrey);
                    font-weight: 700;
                    margin-top: 85px;
                    z-index: 12;
                    font-size: clamp(.9rem, 1vw, 1.1em);
                    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
                    transition: clip-path .2s ease-in-out;
                }

                &:hover{
                    img{
                        filter: grayscale(0);
                        transform: translateY(-10px);
                    }
                    p{
                        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
                    }
                }
            }
            
        }
    }
}