@use './main.scss';

@mixin menuStyle($shift){
    content: '';
    position: absolute;
    background-color: $white;
    width: 30px;
    height: 2px;
    transform: translateY($shift);
    transition: transform .3s ease-in-out, background-color .3s ease-in-out;
}
@mixin mode(){
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-top: -10px;

    background-image: linear-gradient(to left, var(--grey)  50%, transparent 50%);
    border: 1px solid var(--grey);
    @include midScreen{
        background-image: linear-gradient(to left, $white  50%, transparent 50%);
        border: 1px solid $white;
    }
    background-size: 200%;
    background-position: 0%;
    transition: background-position .25s ease-in-out, color .25s ease-in-out;
    

    font-size: clamp(1.40rem, 1vw, 1.50rem);
    overflow: hidden;

    .sun-icon{
        transform: translateY(10px);
        color: var(--grey);
        @include midScreen{
            color: $white;
        }
        transition: all .2s ease-in-out;
    }
    .moon-icon{
        transform: translateY(30px);
        color: var(--grey);
        @include midScreen{
            color: $white;
        }
        transition: all .2s ease-in-out;
    }

    &.active{
        .sun-icon{
            transform: translateY(-30px);
        }
        .moon-icon{
            transform: translateY(-10px);
        }
    }
}

header{
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%;
    z-index: 30;
    transition: transform .01s;


    .indicator{
        position: absolute;
        background-color: $white;
        width: 0px;
        height: 2px;
        bottom: 0 + 8px;

        &.sticky{
            bottom: 0 + 26px;
        }

        @media screen and (max-width: 1024px) {
            background-color: transparent;
        }
    }

    &.sticky{
        padding: 5px 0px 17px 0px;
        margin-top: -100px;
        position: fixed;
        transition: background-color .1s ease-in-out;

        @media screen and (max-width: 1024px){
            padding: 0px 0px 65px 0px;
            height: 50px;
        }

        .hamburger-menu{
            .menu-lines{
                background-color: var(--white);
            }
            .menu-lines::before{
                background-color: var(--white);
            }
            .menu-lines::after{
                background-color: var(--white);
            }
        }

        @include midScreen{
            ul{

                .list{
                    a{
                        color: var(--white);
                    }
                }

                .mode{
                    background-image: linear-gradient(to left, var(--white)  50%, transparent 50%);
                    border-color: var(--white);

                    .sun-icon{
                        color: var(--white);
                    }
                    .moon-icon{
                        color: var(--white);
                    }

                    &:hover{
                        .sun-icon{
                            color: var(--darkGrey);
                        }
                        .moon-icon{
                            color: var(--darkGrey);
                        }
                    }                   
                }
            }
        }
    }
    
    #nav-bar{
        width: 100%;
        max-width: 1440px;
        top: 0;
        transition: top .1s ease-in-out;
        
        @media screen and (max-width: 1024px){

            .hamburger-menu{
                margin-top: 305px;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                width: 60px;
                height: 60px;
                z-index: 21;
                right: 0 + 5px;

                .menu-lines{
                    background-color: $white;
                    width: 30px;
                    height: 2px;
                    transition: transform .3s ease-in-out, background-color .3s ease-in-out;
                }
                .menu-lines::before{
                    @include menuStyle(10px);
                    width: 20px;
                    margin-left: 10px;
                }
                .menu-lines::after{
                    @include menuStyle(-10px);
                }

                .menu-lines.active{
                    background-color: var(--darkGrey);
                    transform: rotate(-45deg);
                }
                .menu-lines.active::before{
                    background-color: var(--darkGrey);
                    transform: scaleX(.01);
                }
                .menu-lines.active::after{
                    background-color: var(--darkGrey);
                    transform: rotate(90deg);
                }
            }

            ul{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                height: 0;
                margin-top: -300px;
                background-color: var(--sW);
                backdrop-filter: blur(5px);
                transform: translateY(0px);
                opacity: 0;
                transition: opacity .4s ease-in-out, transform 0s linear .4s, height 0s linear .4s, background-color .5s ease-in-out;

                .list{
                    list-style: none;
                    margin-top: 50px;
                    transition: opacity .25s ease-in-out;

                    a{
                        text-decoration: none;
                        color: var(--grey);
                        @include midScreen{
                            .nav-text{
                                font-weight: 400;
                            }
                        }

                        .nav-text{
                            font-weight: 400;
                            font-size: .90rem;
                        }
                    }

                    &:hover{
                        opacity: .5;
                    }

                    &.active{
                        opacity: .5;
                    }
                }

                .mode{
                    @include mode();
                    color: var(--grey);
                    border-radius: 50%;
                    margin-top: 50px;

                    &:hover{
                        background-position: 100%;

                        .sun-icon{
                            color: var(--white);
                        }
                        .moon-icon{
                            color: var(--white);
                        }
                    }
                }
                
                &.toggle{
                    height: 101vh;
                    transform: translateY(300px);
                    opacity: 1;
                    transition: opacity .4s ease-in-out, transform 0s linear 0s, height 0s linear 0s, background-color .5s ease-in-out;
                }
            }

            
        }

        @include midScreen{

            ul{
                display: flex;
                justify-content: space-evenly;
                margin-top: 25px;
                margin-left: 50%;

                

                .list{
                    list-style: none;
                    transition: opacity .25s ease-in-out;

                    a{
                        display: flex;
                        color: $white;
                        text-decoration: none;
                        letter-spacing: 0px;

                        .nav-text{
                            font-weight: 400;
                            font-size: .90rem;
                        }
                    }

                    &:hover{
                        opacity: .5;
                    }
                }

                .mode{
                    @include mode();

                    &:hover{
                        background-position: 100%;

                        .sun-icon{
                            color: $black;
                        }
                        .moon-icon{
                            color: $black;
                        }
                    }
                }
            }
        }
    }
}