@use './main.scss';

@mixin shape {
    margin-bottom: -1px;
    position: absolute;
    margin-right: -2000px;
    bottom: 0;
    @include smallScreen{
        margin-right: -1590px;
    }
    @include midScreen{
        margin-right: auto;
        margin-left: -300px;
    }
    @include largeScreen{
        margin-left: -260px;
    }
    @include xlargeScreen{
        margin-left: -210px;
    }
    z-index: 10;
}

@mixin front {
    position: absolute;
    margin-top: 18px;
    bottom: 0;
    width: 500px;
    height: 281px;
    margin-right: -175px;
    @include smallScreen{
        margin-right: -95px;
    }
    @include midScreen{
        width: 750px;
        height: 422px;
        margin-right: -760px;
    }
    @include largeScreen{
        width: 1000px;
        height: 563px;
        margin-right: -850px;
    }
    @include xlargeScreen{
        margin-right: -905px;
    }
    z-index: 9;
    transition: opacity 1s ease-in-out;
}

@mixin back(){
    position: absolute;
    bottom: 0;
    width: 750px;
    height: 259px;
    left: 0;
    margin-left: -115px;
    margin-bottom: 45px;
    @include smallScreen{
        left: auto;
        margin-left: auto;
        width: 1000px;
        height: 345px;
        margin-right: -200px;
        margin-bottom: -9px;
    }
    @include midScreen{
        width: 1250px;
        height: 432px;
        margin-right: -300px;
        margin-bottom: -15px;
    }
    @include largeScreen{
        width: 1500px;
        height: 518px;
        margin-right: -250px;
        margin-bottom: -4px;
    }
    @include xlargeScreen{
        width: 2000px;
        height: 691px;
        margin-right: -325px;
        margin-bottom: -1px;
    }
    z-index: 8;
    transition: opacity 1s ease-in-out;
}

@mixin celestialBody(){
    position: absolute;
    width: 500px;
    height: 281px;
    z-index: 7;
    @include smallScreen{
        width: 600px;
        height: 338px;
    }
    @include midScreen{
        width: 500px;
        height: 281px;
    }
    @include largeScreen{
        width: 600px;
        height: 338px;
    }
    @include xlargeScreen{
        width: 700px;
        height: 394px;
    }
    transition: opacity 1s ease-in-out;
    transition: top 1s ease-in-out;
}

@mixin cloud(){
    position: absolute;
    width: 138px;
    height: 99px;
    top: 0 + 50px;
    left: 0;
    margin-left: 100px;
    transform: scaleX(-1);
    z-index: 8;
    animation: xsDrift 50s infinite alternate;
    @include smallScreen{
        animation: sDrift 50s infinite alternate;
    }
    @include midScreen{
        top: auto;
        left: auto;
        width: 188px;
        height: 135px;
        bottom: 0 + 350px;
        margin-right: -325px;
        margin-left: auto;
        animation: mDrift 60s infinite alternate;
    }
    @include largeScreen{
        width: 238px;
        height: 171px;
        bottom: 0 + 500px;
        margin-right: -300px;
        animation: lgDrift 60s infinite alternate;
    }
    @include xlargeScreen{
        bottom: 0 + 550px;
        margin-right: -350px;
        animation: xlgDrift 60s infinite alternate;
    }

    transition: opacity 1s ease-in-out;
}

#home{

    .backround-imgs{
        overflow: hidden;
        width: 100%;
        background-color: $blue;
        background-image: $blue-gradient;
        height: 90vh;
        @include smallScreen{
            height: 95vh;
        }
        @include largeScreen{
            height: 90vh;
        }
        &::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 5px;
            background-color: var(--white);
            margin-top: -2px;
            transition: background-color .5s ease-in-out;
        }

        .all-imgs{
            display: flex;
            justify-content: flex-end;
            @include midScreen{
                display: flex;
                justify-content: center;
            }
            position: relative;
            width: 100%;
            height: 100%;

            .nightGradient{
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: $darkBlue;
                background-image: $darkBlue-gradient;
                transition: opacity 1s ease-in-out;
            }

            .white-block{
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 0;
                background-color: var(--white);
                z-index: 30;
            }

            .dayShape{
                @include shape();
            }
            .front-Day{
                @include front();
            }
            .back-Day{
                @include back();
            }

            .nightShape{
                @include shape();
                transition: opacity .5s ease-in-out;
            }
            .front-Night{
                @include front();
            }
            .back-Night{
                @include back();
            }

            .celestialBodies{
                position: absolute;
                left: 0 - 150px;
                width: 200px;
                height: 200px;
                top: 0 - 145px;
                margin-right: 400px;
            
                @include smallScreen{
                    left: 0 - 170px;
                    top: 0 - 160px;
                }
                @include midScreen{
                    left: auto;
                    top: auto;
                    width: 500px;
                    height: 500px;
                    bottom: 0 - 160px;
                    margin-right: -70px;
                }
                @include largeScreen{
                    width: 600px;
                    height: 600px;
                    bottom: 0 - 90px;
                    margin-right: 70px;
                }
                @include xlargeScreen{
                    width: 750px;
                    height: 750px;
                    bottom: 0 - 220px;
                    margin-right: 150px;
                }
                .sun-img{
                    @include celestialBody();
                    margin-top: 60px;
                    margin-left: -50px;
                    @include midScreen{
                        margin-left: 250px;
                        margin-top: -140px;
                    }
                    @include largeScreen{
                        margin-left: 300px;
                        margin-top: -160px;
                    }
                    @include xlargeScreen{
                        margin-left: 390px;
                        margin-top: -190px;
                    }
                }
                .moon-img{
                    @include celestialBody();
                    margin-top: -140px;
                    margin-left: -250px;
                    @include smallScreen{
                        margin-top: -200px;
                        margin-left: -350px;
                    }
                    @include midScreen{
                        margin-left: -250px;
                        margin-top: 360px;
                    }
                    @include largeScreen{
                        margin-left: -290px;
                        margin-top: 420px;
                    }
                    @include xlargeScreen{
                        margin-left: -340px;
                        margin-top: 550px;
                    }
                }

                &.toggle{
                    transform: rotate(-180deg);

                    .moon-img{
                        transform: rotate(180deg);
                    }
                }
                &.transition{
                    transform: rotate(-360deg);

                    .moon-img{
                        transform: rotate(360deg);
                    }
                }
            }

            .cloud-img{
                @include cloud();
            }

            .stars-img{
                position: absolute;
                transform: scale(.75, .75);
                transition: opacity 1s ease-in-out;
                bottom: 0;
                z-index: 6;
                margin-right: -700px;
                margin-bottom: -200px;
                @include midScreen{
                    margin-right: 0;
                }
            }
        }
    }

    .home-container1{
        display: flex;
        justify-content: center;
        align-items: flex-start;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        z-index: 20; 
        
        .home-container2{
            width: 100%;
            max-width: 1440px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include  midScreen{
                justify-content: flex-start;
            }
            

            .home-text{
                display: inline-block;
                color: $white;
                @include smallScreen{
                    margin-left: 50px;
                }
                @include midScreen{
                    margin-bottom: 50px;
                }

                h1{
                    font-weight: 700;
                    font-size: clamp(2.75rem, 8vw, 6rem);
                }
                h2{
                    font-family: 'Poppins', sans-serif;
                    font-weight: 400;
                    font-size: clamp(1.25rem, 1.75vw, 1.5rem);
                }
                h2:nth-child(3){
                    text-align: right;
                    margin-top: 7.5px;
                    @include largeScreen{
                        margin-right: 12px;
                    }
                }
                
                .home-btn{
                    cursor: pointer;
                    margin-left: auto;
                    margin-right: auto;
                    background: none;
                    border: 1px solid $white;
                    border-radius: 25px;
                    margin-top: 30px;
                    width: 180px;
                    height: 45px;

                    @include smallScreen{
                        width: 210px;
                        height: 50px;
                    }
                    @include midScreen{
                        width: 250px;
                        height: 60px;
                        border-radius: 50px;
                    }
                    @include largeScreen{
                        width: 250px;
                        height: 60px;
                    }
                    @include xlargeScreen{
                        width: 250px;
                        height: 60px;
                    }

                    //Button
                    background-image: linear-gradient(to left, $white  50%, transparent 50%);
                    background-size: 300%;
                    background-position: 0%;
                    transition: background-position .25s ease-in-out, color .25s ease-in-out;

                    //Text
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: $white;
                    
                    span:first-child{
                        font-weight: 400;
                        font-size: clamp(.60rem, 1.25vw, .80rem);
                        letter-spacing: 2px;
                        margin-left: 10px;
                    }
                    span:last-child{
                        display: flex;
                        align-items: center;
                        font-size: clamp(.9rem, 1.25vw, 1.2rem);
                        margin-left: 5px;
                        transition: transform .25s ease-in-out;
                    }
                    &:hover{
                        color: $black;
                        background-position: -50%;

                        span:last-child{
                            transform: translateX(10px);
                        }
                    }
                }

                
            }
        }
    }
}


@keyframes xsDrift{
    0%{
        transform: translateX(-90px);
    }
    100%{
        transform: translateX(-10px);
    }
}
@keyframes sDrift{
    0%{
        transform: translateX(-25px);
    }
    100%{
        transform: translateX(40px);
    }
}
@keyframes mDrift{
    0%{
        transform: translateX(15px);
    }
    100%{
        transform: translateX(180px);
    }
}
@keyframes lgDrift{
    0%{
        transform: translateX(65px);
    }
    100%{
        transform: translateX(220px);
    }
}
@keyframes xlgDrift{
    0%{
        transform: translateX(15px);
    }
    100%{
        transform: translateX(220px);
    }
}