@use './main.scss';

#about{
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;

    .about-container{
        margin-top: 150px;
        width: 100%;
        max-width: 1340px;

        .about-header{
            display: flex;
            justify-content: center;

            .about-title{
                font-weight: 700;
                font-size: clamp(2rem, 1.5vw, 2.5rem);
                color: var(--darkGrey);
                @include textShadow;
                @include underline(40px);
                @include xlargeScreen{
                    @include underline(42px);
                }
            }
        }

        .about-body{
            display: flex;
            flex-direction: column;
            align-items: center;
            flex-shrink: 0;
            margin-top: 50px;
            z-index: 10;
            @include bottomEffect;
            @include midScreen{
                flex-direction: row;
                align-items: flex-start;
            }

            .prof-pic{
                width: 200px;
                box-shadow: 15px 15px var(--darkWhite);
                margin: 50px 0px -300px 0px;
                transition: box-shadow .5s ease-in-out;

                @include smallScreen{
                    width: 250px;
                    box-shadow: 20px 20px var(--darkWhite);
                }
                @include midScreen{
                    margin: 50px 0px 0px 100px;
                }
                @include largeScreen{
                    width: 300px;
                    box-shadow: 25px 25px var(--darkWhite);
                }
                @include xlargeScreen{
                    width: 350px;
                }
            }

            .about-text{
                display: flex;
                flex-direction: column;
                align-items: center;
                @include midScreen{
                    align-items: flex-start;
                }
                margin-top: 330px;
                @include smallScreen{
                    margin-top: 340px;
                }
                @include midScreen{
                    margin-top: 65px;
                }
                @include largeScreen{
                    margin-top: 110px;
                }
                @include xlargeScreen{
                    margin-top: 130px;
                }

                .about-subTitle{
                    font-weight: 700;
                    font-size: clamp(1.5rem, 2vw, 1.75rem);
                    color: var(--darkGrey);
                    margin: 0px 25px 0px 25px;
                    @include textShadow;
    
                    @include smallScreen{
                        margin: 0px 50px 0px 50px;
                    }
                    @include midScreen{
                        margin: 0px 50px 0px 50px;
                    }
                    @include largeScreen{
                        margin: 0px 50px 0px 50px;
                    }
                    @include xlargeScreen{
                        margin: 0px 50px 0px 50px;
                    }
                }
    
                .about-paragraph{
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    position: relative;
                    font-family: 'Poppins', sans-serif;
                    text-align: center;
                    font-size: clamp(.9rem, 2vw, 1rem);
                    line-height: 1.75rem;
                    color: var(--grey);
                    margin: 15px 25px 0px 25px;
    
                    @include smallScreen{
                        margin: 15px 50px 0px 50px;
                    }
                    @include midScreen{
                        text-align: left;
                        justify-content: flex-start;
                    }
    
                    .about-btn{
                        display: flex;
                        justify-content: center;
                        margin-top: 20px;
                        @include midScreen{
                            justify-content: flex-start;
                        }
                        background: none;
                        text-decoration: none;
                        border: none;
    
                        a{
                            cursor: pointer;     
                            background: none;
                            text-decoration: none;
                            border: 1px solid var(--darkGrey);
                            border-radius: 10px;
                            width: 230.75px;
                            height: 50px;
                            margin: 0px 50px 0px 50px;
            
                            @include midScreen{
                                width: 260.75px;
                                height: 55px;
                                margin: 0px 0px 0px 0px;
                                border-radius: 12.5px;
                            }
                            @include largeScreen{
                                width: 270.75px;
                            }
            
                            //Button
                            background-image: linear-gradient(to left, var(--darkGrey)  50%, transparent 50%);
                            background-size: 300%;
                            background-position: -50%;
                            transition: background-position .25s ease-in-out, color .25s ease-in-out;
            
                            //Text
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            color: var(--white);
                            
                            span:first-child{
                                font-weight: 400;
                                font-size: clamp(.60rem, 1.25vw, .80rem);
                                letter-spacing: 2px;
                                margin-left: 10px;
                            }
                            span:last-child{
                                display: flex;
                                align-items: center;
                                font-size: clamp(1.1rem, 1.25vw, 1.3rem);
                                margin-left: 10px;
                                transition: transform .25s ease-in-out;
                            }
                            
                            &:hover{
                                color: var(--black);
                                background-position: 0%;
            
                                span:last-child{
                                    transform: translateY(5px);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}