@use './main.scss';

.text{
    display: flex;
    justify-content: center;
}

#exp{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 650px;
    background-color: var(--lightWhite);
    transition: background-color .5s ease-in-out;
    margin-top: 150px;

    .exp-container{
        width: 100%;
        max-width: 1340px;
        overflow: hidden;

        .exp-header{
            display: flex;
            justify-content: center;
            flex-direction: column;
            margin: 150px 25px 0px 25px;

            @include midScreen{
                margin: 150px 50px 0px 50px;
            }

            .exp-title{
                text-align: center;
                font-weight: 700;
                font-size: clamp(2rem, 1.5vw, 2.5rem);
                letter-spacing: 1px;
                color: var(--darkGrey);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
            }
            
            .exp-desc{
                text-align: center;
                font-weight: 500;
                font-size: clamp(1.25rem, 1.25vw, 1.5rem);
                letter-spacing: 1px;
                color: var(--grey);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
                @include underline(60px);

                @include smallScreen{
                    @include underline(40px);
                }
            }
        }

        .exp-grid{
            display: flex;
            align-items: center;
            flex-direction: column;
            margin: 50px 25px 0px 25px;

            @include midScreen{
                margin: 50px 50px 0px 50px;
            }

            .exp-card{
                display: flex;
                align-items: flex-start;
                margin: 100px 0px 100px 0px;
                color: var(--black);
                @include leftEffect;

                img{
                    position: absolute;
                    width: 200px;
                    filter: grayscale(1) opacity(.1) blur(1px);
                    margin: -15px 0px 10px 150px;

                    @include smallScreen{
                        margin: -50px 0px 10px 230px;
                    }
                    @include midScreen{
                        width: 225px;
                        margin: -70px 0px 10px 230px;
                    }
                }
                .card-text1{
                    text-align: right;
                    h4{
                        font-weight: 500;
                        font-size: clamp(1.2rem, 2vw, 1.3rem);
                        color: var(--darkGrey);
                        width: 120px;
                        @include textShadow;

                        @include midScreen{
                            width: auto;
                        }
                        
                    }
                    p{
                        font-family: 'Poppins', sans-serif;
                        font-weight: 400;
                        font-size: clamp(.9rem, 2vw, 1rem);
                        color: var(--grey);
                        margin-top: 7.5px;
                        @include midScreen{
                            margin-top: 0px;
                        }
                    }
                }
                .card-text2{
                    h5{
                        font-weight: 700;
                        font-size: clamp(1.2rem, 2vw, 1.3rem);
                        color: var(--darkGrey);
                        @include textShadow;
                    }
                    p{
                        font-family: 'Poppins', sans-serif;
                        font-weight: 400;
                        font-size: clamp(.9rem, 2vw, 1rem);
                        color: var(--grey);
                        margin-top: 7.5px;
                        @include midScreen{
                            margin-top: 0px;
                        }
                    }
                }
                .dot{
                    width: 20px;
                    height: 20px;
                    background-color: var(--darkGrey);
                    border-radius: 50%;
                    margin: 15px 15px 0px 15px;
                    flex-shrink: 0;
                }
            }
        }
    }
}